import React, {Component} from 'react'

class Home extends Component {
  constructor(props) {
    super(props)
    this.state = {
      id: ''
    }
  }

	generate = () => {
    this.props.history.push(`/edit/${this.state.id}`)
	}

	updateId = (e) => {
    this.setState({
      id: e.target.value
    })
  }

	render() {
		return (<div className="container">
	  	<h3 className="page-title">Generate your own profile from jigsaw</h3>
		  <div className="inline-form">
		    <div className="inline-form-input">
		      <input type="text" placeholder="Your jigsaw ID here. e.g. 14323" value={this.state.id} onChange={this.updateId}/>
		    </div>
		    <div className="inline-form-action">
		      <button onClick={this.generate} className="button">Generate</button>
		    </div>
		  </div>
	  </div>)
	}
}

export default Home


